/*=================== HEADER =========================*/

.header {
  position: absolute;

  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
  &::before {
    // content: '';
    // position: absolute;
    // top: 0;
    // left: 0;
    // width: 100%;
    // height: 100px;
    // z-index: 2;
  }
  &__wrapper {
    // position: relative;
    display: flex;
    height: 72px;
  }

  &__logo {
    z-index: 3;
  }

  &__img {
    width: 101px;
    height: 25px;
  }

  @include desktop {
    top: 40px;
  }
}

.page__link {
  @include fonts(400, 18, 20.92);

  font-family: 'CaviarDreams';
  color: $primaryTextColor;
}

.sub-page__link {
  @include fonts(400, 16, 18.59);

  font-family: 'CaviarDreams';
  color: $primaryTextColor;
}

.select {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  border: none;
  background-color: transparent;
  outline: none;
  z-index: 4;
  @include fonts(700, 14, 17.73);

  font-family: 'CaviarDreams';
  color: #bdbdbd;
}
.close-mobile-menu-btn,
.open-mobile-menu-btn,
.open-mobile-phone-btn {
  display: inline-flex;
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  z-index: 100;
}

.contacts__item:not(:last-child) {
  margin-bottom: 8px;
}

.contacts__link {
  font-family: 'CeraPro';
  font-style: normal;
  color: $secondaryTextColor;
}
@media screen and (max-width: 767px) {
  .header__wrapper {
    display: flex;
    justify-content: space-between;
    position: relative;
    align-items: center;
  }

  .header__logo {
    position: relative;
    z-index: 5;
  }

  .open-mobile-menu-btn {
    z-index: 3;
  }

  .open-mobile-phone-btn {
    z-index: 4;
  }
  .close-mobile-menu-btn {
    margin-bottom: 55px;
  }
  .contacts {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    padding-top: 112px;
    text-align: center;
    z-index: 3;
    opacity: 0;
    transition: all 0.3s ease 0s;
    visibility: hidden;
    pointer-events: none;
  }
  .contacts.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }
  .contacts__icon {
    display: none;
  }

  .decor1,
  .decor2,
  .decor3 {
    position: absolute;
    width: 74px;
    height: 74px;

    z-index: 2;

    box-shadow: 20px 20px 80px rgba(0, 0, 0, 0.1);
  }
  .decor1 {
    left: 0px;
    top: 0;
    border-radius: 0px 50px 50px 0px;
  }

  .decor2 {
    right: 0px;
    top: 0;
    border-radius: 50px 0px 0px 50px;
  }

  .decor3 {
    left: -25px;
    top: 295px;
    border-radius: 0px 0px 30px 0px;
  }
}
@media screen and (max-width: 1439.98px) {
  .page__nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    padding-top: 24px;
    text-align: center;
    z-index: 4;
    opacity: 0;
    transition: all 0.3s ease 0s;
    visibility: hidden;
    pointer-events: none;
  }

  .page__nav.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }
  .page__link {
    @include fonts(700, 24, 30.17);
    text-transform: uppercase;
    font-family: 'CeraPro';
    color: $secondaryTextColor;
  }

  .page__link--active {
    color: transparent;

    -webkit-text-stroke: 1px $secondaryTextColor;
  }

  .sub-page__link {
    @include fonts(500, 24, 30.17);
    font-family: 'CeraPro';
    color: $secondaryTextColor;
  }
  .sub-page__list {
    // position: relative;

    display: block;
    left: 0;
    top: 18px;
    padding-bottom: 12px;
  }

  .sub-page__item {
    position: relative;
  }

  .sub-page__item::before {
    content: '';
    width: 7px;
    height: 7px;
    border-radius: 3px 0px 3px 0px;
    background-color: $secondaryTextColor;
    display: inline-block;
    margin-right: 9px;
    margin-bottom: 4px;
  }

  .page__item:not(:last-child) {
    margin-bottom: 24px;
  }

  .sub-page__item:not(:last-child) {
    margin-bottom: 24px;
    margin-top: 20px;
  }
}

