@include desktop {
  .header {
    &__wrapper {
      padding: 40px 0px;
      position: relative;
      z-index: 2;
      display: flex;

      align-items: center;
    }

    &__img {
      width: 192px;
      height: 48px;
    }

    &__logo {
      margin-right: 258px;
    }
  }

  .page__nav {
    display: block;
    z-index: 3;
  }

  .page__item {
    position: relative;
  }

  .page__list {
    display: flex;
  }

  .page__item:not(:last-child) {
    margin-right: 56px;
  }

  .page__item:nth-child(2) {
    margin-right: 70px;
  }

  .sub-page__list {
    display: none;
    position: absolute;
    left: 0;
    top: 20px;
    padding-top: 5px;
  }

  .sub-page__item:not(:last-child) {
    margin-bottom: 4px;
  }

  .open-mobile-menu-btn,
  .open-mobile-phone-btn {
    display: none;
  }

  .select {
    margin-left: 130px;
    margin-right: auto;
  }

  .arrow {
    position: absolute;
    right: -12px;
    top: 10px;

    width: 0;
    height: 0;
    border-bottom: 4px solid #008cef;
    border-right: 4px solid transparent;
    border-left: 4px solid transparent;

    transition: all 0.3s ease 0s;
  }

  .page__item:hover .sub-page__list {
    display: block;
  }

  .contacts__icon {
    margin-right: 8px;
  }
  .close-mobile-menu-btn {
    display: none;
  }
}
